.card {

  &--flex {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    position: relative;
    text-decoration: none;
    border-radius: 2px;
  }

  &--white {
    background-color: $color-contrast;
    transition: box-shadow $transition-props;
    box-shadow: 0 2px 20px rgba(152, 136, 196, 0.16);
  }

  &--lg{
    padding: 35px;
  }

  &--md {
    padding: 25px;
  }

  &--xs {
    padding: 15px;
  }

  &--icon {
    height: auto;
    padding: 12px;
    flex-wrap: nowrap;
    flex-direction: row;

    .icon {
      margin-right: 10px;
    }

    .title {
      font-weight: 500;
      font-size: 14px;
      line-height: 1.4;
    }

    .count {
      color: $color-gray-light;
      font-size: 13px;
      line-height: 1.4;
    }
  }

  &--icon-xs {
    height: auto;
    padding: 3px 10px 3px 5px;
    flex-wrap: nowrap;
    flex-direction: row;

    .icon {
      margin-right: 5px;
    }

    .title {
      font-size: 14px;
      color: $color-gray-dark;
    }
  }

  &--line {
    background-color: $color-contrast;
    border-top: 3px solid $color-accent;
    box-shadow: 0 2px 20px rgba(152, 136, 196, 0.16);
    padding: 25px;
  }

  &--outline {
    text-decoration: none;
    border: 1px solid $color-accent-light;
    border-radius: 2px;
    transition: box-shadow $transition-props;
    background: $color-contrast;

    h4 {
      @include typography-subhead;
      margin-bottom: 15px;
    }

    &:hover,
    &:focus {
      box-shadow: $shadow-card;
    }

    .wrap--up {
      padding-top: 30px;
      padding-bottom: 30px;
      border-bottom: 1px solid $color-accent-light;
    }

    .wrap--mid {
      padding: 20px;

      p {
        margin-top: 0;
        margin-bottom: 0;
      }
    }

    .wrap--bot {
      padding: 0 20px 20px;
      margin-top: auto;
      color: $color-accent;

      a {
        font-size: var(--txt-sm-size);
        line-height: var(--txt-sm-lh);
        font-weight: 400;
        margin-top: 0;
        margin-bottom: 0;
      }
    }
  }

  &--support {
    padding: 33px;
    box-shadow: $shadow-card;
    @include tablet {
      padding: 30px;
    }

    @include mobile-md {
      padding: 35px 20px
    }

    p {
      margin-bottom: 0;
      margin-top: 0;
    }

    h2 {
      @include typography-h3;
      margin-top: 10px;
      margin-bottom: 20px;
    }

    .link {
      margin-top: 35px;
    }
  }

  &--dark {
    background: $bg-sand;

    .card__sub {
      margin-top: 20px;
    }
  }

  &--security {
    padding: 0 20px 0 10px;

    img {
      width: 70px;
      display: block;
      margin-top: 15px;
    }

    p {
      min-height: 72px;
      margin-top: 0;
      @include tablet {
        min-height: auto;
      }
    }

    h4 {
      position: relative;
      padding-left: 28px;
      margin-top: 0;
      font-size: 18px;
      margin-bottom: 10px;
      font-weight: 500;
      margin-left: - 30px;

      &::before {
        content: "";
        position: absolute;
        top: 4px;
        left: 0;
        width: 16px;
        height: 16px;
        background-image: url("../img/library/check-round.svg");
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }
    }
  }

  &--download {
    background-color: $color-contrast;
    transition: box-shadow $transition-props;
    box-shadow: $shadow-card;
    border-radius: 2px;
    padding-bottom: 20px;
    min-height: 330px;

    .card__title {
      @include typography-txt;
      font-weight: bold;
      text-align: center;
      white-space: nowrap;
      margin-top: 20px;
    }

    .card__button {
      margin: auto;
      margin-bottom: 0;
      min-width: 0;
      width: calc(100% - 40px)
    }

    .card__icon {
      margin-left: auto;
      margin-right: auto;
      margin-top: 20px;
      margin-bottom: 20px;
      overflow: hidden;
      width: 65px;
      height: 65px;
    }

    .card__qr {
      text-align: center;
      margin-bottom: -10px;

      svg {
        width: 100%;
        display: block;
      }
    }

    .card__link {
      @include typography-txt-sm;
      margin-top: auto;
      text-align: center;
    }

    .card__select {
      margin-top: auto;
      margin-left: auto;
      margin-right: auto;
      width: calc(100% - 40px);
    }

    .card__select + .card__link {
      margin-top: 0;
    }
  }

  &--w-sm {
    max-width: 250px;
  }

  &--w-md {
    max-width: 290px;
  }

  &--w-lg {
    max-width: 310px;
  }
}